.opacityFone {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
}
.modalContent {
  width: 50%;
  height: 50%;
  padding: 10px 20px;
  background-color: black;
  border: 1px solid wheat;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (max-width: 1000px) {
  .modalContent {
    height: 80%;
  }
}
